import HORARIO_ASIGNATURAS from "../static/horario_asignaturas.json";

import { Dispatch, useCallback, useEffect, useMemo, useRef, useState } from "react";
import { Box, Button, ButtonGroup, Chip, Divider, Fab, FormControl, IconButton, InputLabel, ListItemIcon, ListItemText, Menu, MenuItem, Paper, Popover, Select, Table, TableBody, TableCell, TableContainer, TableHead, Typography } from "@mui/material";
import { HorarioTableRow as TableRow, HorarioTableHeadRow as TableHeadRow } from "./style/HorarioTable";
import { AccountCircle, Add, Clear, Delete, FileUpload, FormatColorReset, MeetingRoom, MoreVert, Palette, People, Person, Save, SwapVert, Visibility, VisibilityOff } from "@mui/icons-material";
import { saveAs } from "file-saver";
import { MuiColorInput } from "mui-color-input";
import Color from "color";

import * as Random from "../utils/Random";
import * as Time from "../utils/Time";

import "./Horario.css";

const DIAS = ["Lunes", "Martes", "Miércoles", "Jueves", "Viernes"];
const SIGLA_COLORS: { [sigla: string]: string } = {
	MAT: "#00838f",
	FIS: "#00838f",
	IWI: "#2e58a7",
	INF: "#2e58a7",
	IWN: "#6ba8d1",
	ICS: "#6ba8d1",
	IWG: "#c54b73",
	DEW: "#90aa3a",

	// INF
	INF239: "#fdde15",
	INF236: "#fdde15",
	INF225: "#fdde15",
	INF322: "#fdde15",
	INF260: "#991b1e",
	INF270: "#991b1e",
	INF292: "#991b1e",
	INF293: "#991b1e",
	INF266: "#991b1e",
	INF360: "#991b1e",
	INF245: "#6d57a5",
	INF280: "#00ad5d",
};

const getRamo = (sigla: string, paralelo: string) => HORARIO_ASIGNATURAS.find(r => r.sigla === sigla && r.paralelo === paralelo) ?? null;
const getRamoColor = (sigla: string) => SIGLA_COLORS[sigla] ?? SIGLA_COLORS[sigla.slice(0, 3)] ?? "#000";
const setRamoFlag = (ramos: Ramo[], sigla: string | undefined, flag: keyof Ramo, value: any) => [
	...ramos.map(r => {
		let equals = r.sigla === sigla?.replace("!", "");
		if (sigla?.startsWith("!")) equals = !equals;

		if (sigla === undefined || equals)
			if (!value) delete r[flag];
			//@ts-ignore
			else r[flag] = value;
		return r;
	}),
];

export default function Horario() {
	const [selectedRamos, setSelectedRamos] = useState<Ramo[]>([]);

	return (
		<div>
			<ActionButtons setSelectedRamos={setSelectedRamos} selectedRamos={selectedRamos} />
			<TableContainer component={Paper} style={{ overflowX: "hidden" }}>
				<Table size="small">
					<TableHead>
						<TableHeadRow>
							<TableCell width="8%" align="center" key={-1}>
								Bloques
							</TableCell>
							{DIAS.map((d, i) => (
								<TableCell align="center" key={i}>
									{d}
								</TableCell>
							))}
						</TableHeadRow>
					</TableHead>
					<TableBody>
						{[...Array(20).keys()].map(i => {
							i++;
							return (
								<TableRow>
									<TableCell key={-1}>
										<div
											style={{
												display: "flex",
												justifyContent: "space-around",
											}}
										>
											<span>{i}</span>
											<span style={{ opacity: i % 2 == 0 ? 0.2 : 0.6, transform: "scale(2)" }}>{Time.MinutesToHHMM(495 + 35 * (i - 1) + 15 * Math.floor(i / 2) + (i > 8 ? 45 : 0))}</span>
										</div>
									</TableCell>
									{DIAS.map((_, dia) => {
										const bloques = selectedRamos
											.filter(r => !r.hidden)
											.map(r => r.horario.map(b => ({ ...b, ramo: r })))
											.flat()
											.filter(b => b.bloque === i && b.dia === dia);
										if (!bloques) return <TableCell key={dia}></TableCell>;

										return (
											<TableCell
												width={100 / 6 + "%"}
												className={[
													//
													bloques.length > 1 ? "Horario-tope_warning" : "",
													bloques.some(b => b.ramo.ghost) ? "ghosted" : "",
													{
														"-1": "ex_catedra",
														"0": "",
														"1": "ex_ayudantia",
													}[
														Math.max(
															-1,
															Math.min(
																1,
																bloques.map<number>(b => (b.tipo === "Ayu" ? 1 : -1)).reduce((prev, curr) => prev + curr, 0)
															)
														).toString()
													],
												].join(" ")}
												style={{
													//
													borderLeft: "4px solid #fff6",
													position: "relative",
												}}
												key={dia}
											>
												{i === 1 ? <Box position="absolute" top="0" left="0" style={{ background: "linear-gradient(black, transparent)", overflow: "hidden" }} width="calc(100% + 4px)" height="150%" /> : <></>}
												{i === 20 ? <Box position="absolute" bottom="0" left="0" style={{ background: "linear-gradient(transparent, black)", overflow: "hidden" }} width="calc(100% + 4px)" height="250%" /> : <></>}
												<Box position="relative" display="flex" flexDirection="row" justifyContent="center" alignItems="center" gap="10px" height="30px">
													{bloques.map((bloque, i) => (
														<Bloque //
															bloque={bloque}
															ramo={bloque.ramo}
															selectedRamos={selectedRamos}
															setSelectedRamos={setSelectedRamos}
															key={i}
														/>
													))}
												</Box>
											</TableCell>
										);
									})}
								</TableRow>
							);
						})}
					</TableBody>
				</Table>
			</TableContainer>
		</div>
	);
}

function RamoButton(props: { ramo: Ramo; setSelectedRamos: Dispatch<React.SetStateAction<Ramo[]>> }) {
	const { ramo, setSelectedRamos } = props;
	const ramosParalelos = useMemo(() => HORARIO_ASIGNATURAS.filter(r => r.sigla === ramo.sigla), [ramo]);
	const [bloqueMenuAnchor, setBloqueMenuAnchor] = useState<null | HTMLElement>(null);

	const ramoRef = useRef<any>(null);
	const ramoColor = ramo.color ?? getRamoColor(ramo.sigla);

	const handleMenuClose = () => {
		setBloqueMenuAnchor(null);
	};
	const handleUnfocus = () => setSelectedRamos(curr => setRamoFlag(curr, "!" + ramo.sigla, "ghost", false));

	const changeParalelo = (paralelo: string) =>
		props.setSelectedRamos(curr => {
			curr[curr.indexOf(ramo)] = ramosParalelos.find(r => r.paralelo == paralelo)!;
			return [...curr];
		});

	return (
		<>
			<Button
				ref={ramoRef}
				variant="contained" //
				style={{
					padding: "5px 20px",
					backgroundColor: Color(ramoColor)
						.darken(ramo.hidden ? 0.5 : 0)
						.hex(),
				}}
				onClick={e => setBloqueMenuAnchor(e.currentTarget)}
				onMouseEnter={ev => setSelectedRamos(curr => setRamoFlag(curr, "!" + ramo.sigla, "ghost", true))}
				onMouseLeave={handleUnfocus}
				onBlur={handleUnfocus}
				endIcon={<MoreVert />}
			>
				{ramo.sigla}
			</Button>
			<Menu
				anchorEl={bloqueMenuAnchor}
				open={Boolean(bloqueMenuAnchor)} //
				onClose={handleMenuClose}
				keepMounted
			>
				<Box display="flex" flexDirection="column" margin={2} gap={1}>
					<Box margin={1} display="flex" flexDirection="column" gap={1}>
						<Typography variant="body1">
							<b>{ramo.nombre}</b>{" "}
							<span style={{ opacity: 0.6 }}>
								<i>({ramo.sigla})</i>
							</span>
							<br />
							<span style={{ opacity: 0.4 }}>DEPTO. DE {ramo.departamento}</span>
						</Typography>
						<Divider />
						<Chip icon={<AccountCircle />} label={ramo.profesor} />
						<Chip icon={<People />} label={`${ramo.cupo} POR PARALELO`} />
					</Box>
					<FormControl
						fullWidth
						style={{
							minWidth: "15vw",
						}}
					>
						<InputLabel>Paralelo</InputLabel>
						<Select
							fullWidth
							size="small"
							value={ramo.paralelo}
							label="Paralelo" //
							onChange={ev => changeParalelo(ev.target.value)}
						>
							{ramosParalelos
								.sort((a, b) => +a.paralelo - +b.paralelo)
								.map(r => (
									<MenuItem value={r.paralelo}>{r.paralelo}</MenuItem>
								))}
						</Select>
					</FormControl>
					<Box position="relative" width="100%">
						<Box display="flex" flexDirection="row" alignItems="center" gap={1} style={{ float: "left" }} width="50%">
							{!ramo.color ? (
								<></>
							) : (
								<IconButton style={{ height: "auto" }} onClick={_ => props.setSelectedRamos(curr => setRamoFlag(curr, ramo.sigla, "color", false))}>
									<FormatColorReset />
								</IconButton>
							)}
							<MuiColorInput fullWidth format="hex" value={ramo.color ?? getRamoColor(ramo.sigla)} onChange={value => props.setSelectedRamos(curr => setRamoFlag(curr, ramo.sigla, "color", value))} />
						</Box>
						<Box style={{ float: "right" }}>
							<IconButton onClick={_ => props.setSelectedRamos(curr => setRamoFlag(curr, ramo.sigla, "hidden", !ramo.hidden))} title={`${ramo.hidden ? "Mostrar" : "Ocultar"} ${ramo.sigla}`}>
								{ramo.hidden ? <Visibility fontSize="small" /> : <VisibilityOff fontSize="small" />}
							</IconButton>
							<IconButton onClick={_ => void handleMenuClose() || props.setSelectedRamos(curr => curr.filter(r => r.sigla !== ramo!.sigla))} title={`Eliminar ${ramo.sigla}`}>
								<Delete fontSize="small" />
							</IconButton>
						</Box>
					</Box>
				</Box>
			</Menu>
		</>
	);
}

function ActionButtons(props: { selectedRamos: Ramo[]; setSelectedRamos: Dispatch<React.SetStateAction<Ramo[]>> }) {
	const uploadInput = useRef<HTMLInputElement>(null);

	return (
		<Box margin="10px" gap="10px" display="flex" position="relative">
			<Typography variant="h5" margin="0 50px" alignContent="center">
				Generador de Horario
			</Typography>
			<Button
				variant="contained"
				startIcon={<Add />}
				onClick={() => {
					const res = prompt("Introduce SIGLAS DE RAMO y PARALELO");
					if (!res) return;
					const [sigla, paralelo] = res
						.trim()
						.toUpperCase()
						.split(/\s+|,/g)
						.filter(_ => _);
					const ramo = getRamo(sigla, paralelo);
					if (!ramo) return alert("Ramo no encontrado.");
					props.setSelectedRamos(curr => [...curr, ramo]);
				}}
			>
				Añadir ramo
			</Button>
			<ButtonGroup variant="contained">
				<Button
					variant="outlined"
					title="Guardar horario"
					onClick={() => {
						if (!props.selectedRamos) return;

						const chars = JSON.stringify(props.selectedRamos.map(r => [r.sigla, r.paralelo]));
						let bytes = new Array(chars.length);
						for (var i = 0; i < chars.length; i++) bytes[i] = chars.charCodeAt(i);

						saveAs(new Blob([new Uint8Array(bytes)]), "horario.husm");
					}}
				>
					<Save />
				</Button>
				<Button variant="outlined" title="Cargar horario" onClick={() => uploadInput.current?.click()}>
					<input
						ref={uploadInput}
						style={{ display: "none" }}
						accept=".husm"
						type="file"
						onChange={async ev =>
							ev.target.files?.[0] &&
							Promise.resolve(
								props.setSelectedRamos(
									JSON.parse((await ev.target.files[0]?.text()) ?? []).map(
										(
											r: [string, string] //
										) => HORARIO_ASIGNATURAS.find(ramo => ramo.sigla === r[0] && ramo.paralelo === r[1])
									)
								)
							).catch(() => {})
						}
					/>
					<FileUpload />
				</Button>
			</ButtonGroup>
			<Button variant="outlined" color="error" startIcon={<Clear />} onClick={() => window.confirm("Limpiar horario actual?") && props.setSelectedRamos([])}>
				Limpiar
			</Button>
			<Box
				position="absolute"
				right="0"
				height="calc(100% + 10px * 2)"
				margin="-10px"
				alignContent="center"
				sx={{
					backgroundColor: "#000000",
				}}
			>
				<Box margin="0 20px" gap="10px" display="flex" position="relative" style={{ opacity: props.selectedRamos.length ? 1 : 0 }}>
					<Typography variant="h6" margin="0 20px" alignContent="center">
						Ramos asignados
					</Typography>
					{props.selectedRamos
						.sort((a, b) => Random.decimalHash(getRamoColor(a.sigla)) - Random.decimalHash(getRamoColor(b.sigla)))
						.map((ramo, i) => (
							<RamoButton key={i} setSelectedRamos={props.setSelectedRamos} ramo={ramo} />
						))}
				</Box>
			</Box>
		</Box>
	);
}

function Bloque(props: { bloque: Bloque; ramo: Ramo; selectedRamos: Ramo[]; setSelectedRamos: Dispatch<React.SetStateAction<Ramo[]>> }) {
	const { bloque, ramo, selectedRamos, setSelectedRamos } = props;

	const isGhost = ramo.ghost;
	const isBloqueAyudantía = bloque.tipo === "Ayu";
	const isContinuation = useMemo(() => ramo.horario.some(b => b.dia === bloque.dia && b.tipo === bloque.tipo && b.bloque === bloque.bloque - 1), [ramo, bloque]);
	const continuations = useMemo<number>(() => {
		let continuations = 0;
		let b = bloque.bloque;
		for (const r of ramo.horario.filter(b => b.dia === bloque.dia && b.tipo === bloque.tipo && b.bloque > bloque.bloque)) {
			if (r.bloque === b + 1) continuations++;
			else break;
			b = r.bloque;
		}
		return continuations;
	}, [bloque]);
	const hasContinuation = continuations > 0;
	const colorRamo = useMemo(() => {
		const col = isBloqueAyudantía ? "#555" : ramo.color ?? getRamoColor(ramo.sigla);
		if (isGhost) return Color(col).mix(Color("#000"), 0.6).hex();
		return col;
	}, [selectedRamos]);
	const collisions = selectedRamos
		.map(r => r.horario)
		.flat()
		.filter(b => b.bloque === bloque.bloque && b.dia === bloque.dia).length;

	return (
		<>
			<Box
				onMouseEnter={_ => setSelectedRamos(curr => setRamoFlag(curr, "!" + ramo.sigla, "ghost", true))}
				onMouseLeave={_ => setSelectedRamos(curr => setRamoFlag(curr, "!" + ramo.sigla, "ghost", false))}
				style={{
					...(isBloqueAyudantía &&
					selectedRamos
						.map(r => r.horario)
						.flat()
						.filter(b => b.dia === bloque.dia && b.bloque === bloque.bloque).length === 1
						? {
								width: "50%",
						  }
						: {}),
					backgroundColor: Color(colorRamo).darken(0.3).rotate(-2).hex(),
				}}
				className={["Horario-bloque", hasContinuation && isContinuation ? "middle" : isContinuation ? "bottom" : "top"].join(" ")}
				key={ramo.paralelo}
			>
				{!isContinuation ? (
					<Box className="Horario-bloque-detail" position="absolute" top="0" left="0" margin="-12px -10px" zIndex={20}>
						<Paper style={{ padding: "0 12px", backgroundColor: "#000", border: `3px solid ${colorRamo}`, color: isGhost ? "#666" : "#fff" }}>{bloque.hora}</Paper>
					</Box>
				) : !hasContinuation ? (
					<Box className="Horario-bloque-detail" position="absolute" bottom="0" right="0" margin="-12px -10px" zIndex={20}>
						<Paper style={{ padding: "0 12px", backgroundColor: "#000", border: `3px solid ${colorRamo}`, color: isGhost ? "#444" : "#666" }}>{Time.MinutesToHHMM(Time.HHMMtoMinutes(bloque.hora) + 35 * (continuations + 1))}</Paper>
					</Box>
				) : (
					<></>
				)}
				<Box
					position="relative"
					display="flex"
					flexDirection="row"
					alignItems="center"
					justifyContent="center"
					gap="5px"
					height="100%"
					style={{
						position: "relative",
						top: "-15px",
						height: "100%",
						fontSize: "14px",
						opacity: (bloque.bloque + 2) % 2 == 0 && collisions < 3 ? (!isGhost ? 1 : 0.4) : 0,
						lineHeight: collisions <= 1 && !isBloqueAyudantía ? "unset" : "15px",
					}}
				>
					<Box width="100%">
						<Box
							margin={0}
							style={{
								float: "left",
								transform: `scale(${collisions <= 1 && !isBloqueAyudantía ? "2" : collisions > 2 ? "0.6" : "1"})`,
								marginLeft: collisions <= 1 && !isBloqueAyudantía ? "60px" : collisions > 2 ? "0" : "8px",
							}}
						>
							<span>
								<b>{ramo.sigla.slice(0, collisions > 2 ? 3 : undefined)}</b>
							</span>{" "}
							{collisions <= 1 && !isBloqueAyudantía ? <></> : <br />}
							<span style={{ color: "#000", opacity: 0.8 }}>
								<b>{ramo.paralelo}</b>
							</span>
						</Box>
						<Chip //
							icon={<MeetingRoom fontSize="small" style={{ opacity: 0.5, color: "#fff", height: "80%" }} />}
							label={<i>{bloque.sala.split("-")[0]}</i>}
							style={{
								position: "absolute",
								height: "min-contents",
								right: "5%",
								bottom: `calc(50% + ${collisions <= 1 && !isBloqueAyudantía ? "4px" : "0px"})`,
								backgroundColor: "#000",
								transformOrigin: "right",
								transform: `scale(${collisions <= 1 && !isBloqueAyudantía ? "1.2" : "0.9"}) translateY(50%)`,
							}}
						/>
					</Box>
				</Box>
			</Box>
		</>
	);
}
